html,body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow:auto; /* or overflow:hidden; if dont want scrollbars */
}

#root {
  height: 100%
}

.map {
  height: 100vmax;
}

.location-form {
  position: absolute;
  top: 0;
  right: 0;
}